










import ut from '../utils/index.js';

export default {
	props: ["type", "orderId", "reload", "changeVisibileModal"],
	data() {
		return {
			statusList: [],
		}
	},
	onLoad() {
		this.requestData();
	},
	methods: {
		requestData() {
			let type = this.type;
			let orderId = this.orderId;
			ut.request({
				method: 'get',
				url: `customze/store/order/getOrderStatusRecords?type=${type}&orderId=${orderId}`
			}).then(data => {
				console.log(data)
				this.statusList = data;
			})
		}
	}
}
