export default {
  props: ["orderId","reload","changeVisibileModal","confirmPlan"],
  data() { 
	return {

	}
  },
  methods: { 
	 cancel_order() {
	 			
		ut.request({
			data: {
				orderId: this.orderId,
			},
			url: "customze/store/order/cancelOrder"
		}).then(data=>{
			this.$parent.changeVisibileModal(false)
			this.$emit('reload');
			ut.totast("订单取消成功")
		})
	},
	 cancel_confirm() {
		 this.$parent.changeVisibileModal(false)
		this.$emit('changeb',true)
	 },
	againPlan() {
		ut.request({
			data: {
				orderId: this.orderId
			},
			url: "customze/store/order/againPrice"
		}).then(data=>{
			this.$parent.changeVisibileModal(false)
			this.$emit('reload');
			ut.totast("操作成功")
			console.log(data)
		})
	},
	confirmCustomPlan() {
		ut.request({
			data: {
				orderId: this.orderId
			},
			url: "customze/store/order/confirmPrice"
		}).then(data=>{
			console.log(data);
			let that = this;
			wx.requestPayment({
				'timeStamp': data.timeStamp,
				'nonceStr': data.nonceStr,
				'package': data.package,
				'paySign': data.paySign,
				'signType': 'HMAC-SHA256',
				success: function(res) {
					
				},
				'fail': function(res) {
					console.log(res)
				},
				'complete': function(res) {
					that.$parent.changeVisibileModal(false)
					that.$emit('reload');
				}
			});
		})
	}
  }
}
