





























































import ut from '../utils/index.js';

export default {
	props: ["reason", "orderId", "reload", "changeVisibileModal", "type"],
	data() {
		return {
			chooseData: null,
			detail: '',
			storeStar: 5,
			craftsmanStar: 5,
			carryStar: 5,
			expressStar: 5,
			pictures: [],
			static: ut.static,
			phone: '',
			rateString: ['', '非常差', '很差', '一般', '很好', '非常好'],
			checkArray: [{
				title: '服务态度好',
				checked: false
			}, {
				title: '材料齐全',
				checked: false
			}, {
				title: '信誉度高',
				checked: false
			}, {
				title: '服务态度好',
				checked: false
			}, {
				title: '服务态度好',
				checked: false
			}, {
				title: '服务态度好',
				checked: false
			}]
		}
	},
	methods: {
		choose(data) {
			this.chooseData = data;
		},
		addcomment() {
			let pictures = '',
				hasPicture = 2;
			this.pictures.forEach(item => {
				hasPicture = 1
				if (pictures) {
					pictures += ',' + item
				} else {
					pictures += item
				}

			})
			let append = "";
			for (let item of this.checkArray) {
				if (item.checked) {
					append += item.title + ",";
				}
			}
			const userinf = wx.getStorageSync('userinf')
			ut.request({
				data: {
					"account": userinf.nickname || userinf.name,
					"content": this.detail,
					"hasPicture": hasPicture, //是否有图 1有 2无
					"photo": userinf.photo,
					"pictures": pictures,
					"type": this.type, //1商品2服务3定制
					"review": "2", //是否为追评 1是 2否
					"orderId": this.orderId, //订单id
					"starType": this.starType, // 星级类型，1-商户 2-工匠 3-搬运 4-速递
					storeStar: this.storeStar,
					craftsmanStar: this.craftsmanStar,
					carryStar: this.carryStar,
					expressStar: this.expressStar,
					"commentBox": append, // 选项
				},
				url: 'comment/add',
				c: true
			}).then(data => {
				this.$parent.changeVisibileModal(false)
				this.$emit('reload');
				ut.totast("评价成功")
			})
		},
		sub() {
			if (!this.detail) {
				ut.totast("请输入评价")
				return;
			}
			this.addcomment();
		},
		uploadimg() {
			ut.uploadimg({
				callback: res => {
					this.req_uploadimg(res);

				}
			})
		},
		req_uploadimg(res) {
			const that = this;
			wx.uploadFile({
				url: ut.uploadimgurl + "common/imageUpload", //仅为示例，非真实的接口地址
				filePath: res,
				name: 'picture',
				header: {
					"Accept": "*/*"
				},
				success(res) {
					console.log(res)
					if (!res.data) {
						ut.totast('上传图片失败')
						return;
					}
					res.data = JSON.parse(res.data);
					if (res.data.code == 0) {
						that.pictures.push(res.data.data);
					} else {
						ut.totast('上传图片失败')
					}
				}
			})
		},
		change_rate(rate) {
			this.rate = rate;
		},
		change_check(index) {
			let item = this.checkArray[index];
			item.checked = !item.checked;
			this.checkArray = this.checkArray;
		}
	}
}
